}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
